body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #121212;
    color: white;
}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.login-box {
    background-color: #1e1e1e;
    padding: 30px;
    border-radius: 8px;
    width: 400px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    text-align: center;
}

h1 {
    margin-bottom: 20px;
    font-size: 24px;
}

.input-group {
    margin-bottom: 15px;
    text-align: left;
}

.input-group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
}

.input-group input {
    width: 100%;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid #333;
    background-color: #292929;
    color: white;
    font-size: 14px;
}

.input-group input:focus {
    outline: none;
    border-color: #555;
}
.error-message {
    display: block;
    font-size: 16px;
    color: #e0ef6e;
    text-decoration: none;
    margin-bottom: 20px;
}
.forgot-password {
    display: block;
    margin-top: 5px;
    font-size: 12px;
    color: #aaa;
    text-decoration: none;
}

.forgot-password:hover {
    color: white;
}

.login-button {
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #0078d4;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.login-button:hover {
    background-color: #005bb5;
}


.create-account,
.privacy-policy {
    display: block;
    margin-top: 10px;
    font-size: 12px;
    color: #aaa;
    text-decoration: none;
}

.create-account:hover,
.privacy-policy:hover {
    color: white;
}
.logo {
    width: 50px;  /* Adjust the size as needed */
    height: auto;
    margin-bottom: 20px; /* Space between logo and form */
    display: block;
    margin-left: auto;
    margin-right: auto;
}